import { useEffect, useState } from "react";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import toast from "react-hot-toast";

export default function ProviderHome() {
  const [jobs, setJobs] = useState([]);
  const [applications, setApplications] = useState([]);
  const [loading, setLoading] = useState(true);
  const [provider, setProvider] = useState(null);
  const [submitting, setSubmitting] = useState(null);

  useEffect(() => {
    const stored = localStorage.getItem("provider");
    if (stored) setProvider(JSON.parse(stored));
  }, []);

  useEffect(() => {
    if (provider?.id) {
      fetchJobs();
      fetchApplications(provider.id);
    }
  }, [provider]);

  const fetchJobs = async () => {
    try {
      const res = await fetch("https://khedme-api.onrender.com/api/jobs/all");
      const data = await res.json();
      if (data.success) setJobs(data.jobs);
      else throw new Error();
    } catch {
      toast.error("Failed to load jobs âŒ");
    } finally {
      setLoading(false);
    }
  };

  const fetchApplications = async (id) => {
    try {
      const res = await fetch(`https://khedme-api.onrender.com/api/providers/applications/${id}`);
      const data = await res.json();
      if (data.success) setApplications(data.applications);
    } catch {
      console.error("Failed to fetch applications");
    }
  };

  const handleApply = async (jobId) => {
    if (!provider?.id) return toast.error("Please log in as provider first âŒ");
    setSubmitting(jobId);

    try {
      const res = await fetch("https://khedme-api.onrender.com/api/providers/apply", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          job_id: jobId,
          provider_id: provider.id,
          message: "Iâ€™m interested in this job.",
        }),
      });

      const data = await res.json();
      if (data.success) {
        toast.success("Application submitted âœ…");
        fetchApplications(provider.id);
      } else throw new Error(data.message);
    } catch (err) {
      toast.error(err.message || "Failed to apply âŒ");
    } finally {
      setSubmitting(null);
    }
  };

  const isApplied = (jobId) =>
    applications.some((app) => app.job_id === jobId);

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 to-white text-gray-800">
      <div className="max-w-4xl mx-auto px-4 py-10">
        <h1 className="text-3xl font-bold text-orange-600 mb-6">
          Provider Dashboard
        </h1>

        <Tabs defaultValue="jobs" className="w-full">
          <TabsList>
            <TabsTrigger value="jobs">Available Jobs</TabsTrigger>
            <TabsTrigger value="applications">My Applications</TabsTrigger>
          </TabsList>

          {/* âœ… Available Jobs */}
          <TabsContent value="jobs">
            {loading ? (
              <p className="text-gray-400 text-center mt-10">Loading...</p>
            ) : jobs.length === 0 ? (
              <p className="text-gray-400 text-center mt-10">
                No jobs available.
              </p>
            ) : (
              <div className="grid gap-4 mt-6">
                {jobs.map((job) => {
                  const applied = isApplied(job.id);
                  return (
                    <div
                      key={job.id}
                      className="bg-white p-5 rounded-xl shadow-sm border border-orange-100 hover:shadow-md transition"
                    >
                      <div className="flex justify-between items-start">
                        <h2 className="text-lg font-semibold">{job.service}</h2>
                        {applied && (
                          <span className="text-sm text-green-600 font-medium">
                            âœ… Applied
                          </span>
                        )}
                      </div>
                      <p className="text-gray-500 text-sm">
                        District: {job.district}
                      </p>
                      <p className="mt-2 text-gray-600">{job.description}</p>
                      <p className="mt-1 font-semibold text-orange-600">
                        ${job.budget}
                      </p>
                      <button
                        onClick={() => handleApply(job.id)}
                        disabled={submitting === job.id || applied}
                        className={`mt-4 w-full py-2 rounded-lg font-medium transition ${
                          applied
                            ? "bg-gray-300 text-gray-600 cursor-not-allowed"
                            : "bg-orange-500 hover:bg-orange-600 text-white"
                        }`}
                      >
                        {submitting === job.id
                          ? "Submitting..."
                          : applied
                          ? "Applied"
                          : "Apply Now"}
                      </button>
                    </div>
                  );
                })}
              </div>
            )}
          </TabsContent>

          {/* ðŸ§¾ My Applications */}
          <TabsContent value="applications">
            {applications.length === 0 ? (
              <p className="text-center text-gray-500 mt-10">
                You havenâ€™t applied for any jobs yet.
              </p>
            ) : (
              <div className="grid gap-4 mt-6">
                {applications.map((app) => (
                  <div
                    key={app.application_id}
                    className="bg-white p-5 rounded-xl shadow-sm border border-orange-100"
                  >
                    <h2 className="text-lg font-semibold text-orange-600">
                      {app.service}
                    </h2>
                    <p className="text-gray-500 text-sm">
                      District: {app.district}
                    </p>
                    <p className="mt-1 text-gray-600">{app.description}</p>
                    <p className="text-orange-500 font-semibold mt-1">
                      ${app.budget}
                    </p>
                    <p className="text-sm text-gray-400 mt-1">
                      Status:{" "}
                      <span
                        className={`${
                          app.status === "approved"
                            ? "text-green-600"
                            : "text-yellow-600"
                        } font-medium`}
                      >
                        {app.status}
                      </span>
                    </p>
                  </div>
                ))}
              </div>
            )}
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
